import React from 'react';
import { Link } from 'react-router-dom';

function Header() {
    return (
        <header style  = {headerStyle}>
        <h1>ToDo List !$!</h1>
        <Link to = "/">Home</Link> | <Link to = "/about">About</Link>
        </header>

    )
}

const headerStyle = {
    background: "black",
    color: "white",
    textAlign: 'center',
    padding: '10px',
    // marginbottom: '20px'

}
 
export default Header;